<template>
    <a-form :model="form" class="infor-form">
        <a-form-item label="deck name">
            <a-typography-text>
                {{ deck_name }}
            </a-typography-text>
        </a-form-item>
        <a-form-item label="word">
            <a-typography-paragraph editable v-model:editText="word" @edit-end="updateInfors">
                {{ word }}
            </a-typography-paragraph>
        </a-form-item>

        <a-form-item label="meaning">
            <a-typography-paragraph editable v-model:editText="meaning" @edit-end="updateInfors">
                {{ meaning }}
            </a-typography-paragraph>
        </a-form-item>

        <a-form-item>
            <button @click="deleteDeck">Delete</button>
        </a-form-item>


    </a-form>
</template>

<script>

import router from '@/router';
import { inject, onMounted, ref } from 'vue'
export default {
    setup() {
        const word = ref();
        const meaning = ref();
        const deck_name = ref();
       
        const $axios = inject("$axios");


        onMounted(() => {
            $axios({
                url: "",
                method: "post",
                headers: {}
            }).then((res) => {
                console.log(res);
                word.value = res.data.data.word;
                meaning.value = res.data.data.meaning;
                deck_name.value = res.data.data.deck_name;
                
            })
        })

        const updateInfors = () => {
            console.log(word.value);
            console.log(meaning.value);

            $axios({
                url: "",
                method: "post",
                data: "deck_name=" + deck_name.value + "&word=" + word.value + "&meaning=" + meaning.value,
                headers: {

                }
            }).then((res) => {
                console.log(res);
            })
        }

        const deleteDeck=()=>{
            $axios({
                url:"/anki/removeDeck.do",
                method:"post",
                data:"deck_name="+ deck_name.value,
                headers:{

                }
            }).then((res)=>{
                router.push("/home/ShowDecks")
                console.log(res);
            })
        }

        return {
            word,
            deck_name,
            meaning,
            deleteDeck,
            updateInfors
        }
    },
}
</script>

<style scoped>
.infor-form {
    width: 100%;
    height: 100%;
}
</style>