<template>
  <a-space direction="vertical" size="large" :style="{ width: '600px' }">
    <a-radio-group v-model="layout" type="button">
      <a-radio value="horizontal">horizontal</a-radio>
      <a-radio value="vertical">vertical</a-radio>
      <a-radio value="inline">inline</a-radio>
    </a-radio-group>

    <a-form :model="form" :layout="layout">
      <a-form-item field="deckName" label="deckName">
        <a-input v-model="form.deckName" placeholder="please enter the deck name..." />
      </a-form-item>

      <a-form-item field="word1" label="word1">
        <a-input v-model="form.post" placeholder="please enter a word..." />
      </a-form-item>
      <a-form-item field="meaning1" label="meaning1">
        <a-input v-model="form.post" placeholder="please enter it's meaning..." />
      </a-form-item>

      <a-form-item field="word2" label="word2">
        <a-input v-model="form.post" placeholder="please enter a word..." />
      </a-form-item>
      <a-form-item field="meaning2" label="meaning2">
        <a-input v-model="form.post" placeholder="please enter it's meaning..." />
      </a-form-item>

      <a-form-item field="word3" label="word3">
        <a-input v-model="form.post" placeholder="please enter a word..." />
      </a-form-item>
      <a-form-item field="meaning3" label="meaning3">
        <a-input v-model="form.post" placeholder="please enter it's meaning..." />
      </a-form-item>

      <a-form-item field="word4" label="word4">
        <a-input v-model="form.post" placeholder="please enter a word..." />
      </a-form-item>
      <a-form-item field="meaning4" label="meaning4">
        <a-input v-model="form.post" placeholder="please enter it's meaning..." />
      </a-form-item>

      <a-form-item field="word5" label="word5">
        <a-input v-model="form.post" placeholder="please enter a word..." />
      </a-form-item>
      <a-form-item field="meaning5" label="meaning5">
        <a-input v-model="form.post" placeholder="please enter it's meaning..." />
      </a-form-item>


      <a-form-item>
        <a-button @click="handleSubmit">Submit</a-button>
      </a-form-item>
    </a-form>
    <div>
      {{ form }}
    </div>
  </a-space>
</template>
  
<script>
import { inject, reactive, ref } from 'vue';
import { Modal } from '@arco-design/web-vue';

export default {
  name: 'AddToExistingDeck',
  setup() {
    const layout = ref('horizontal')
    const form = reactive({
      deckName: '',
      word1: '', meaning1: '',
      word2: '', meaning2: '',
      word3: '', meaning3: '',
      word4: '', meaning4: '',
      word5: '', meaning5: '',

    })

    const $axios = inject("$axios");

    const handleSubmit = () => {
      console.log(form.deckName);
      $axios({
        url: "/anki/addToExistingDeck.do",
        method: "post",
        data: "deck_name=" + form.deckName + "&word1=" + form.word1 + "&meaning1" + form.meaning1
          + "&word2=" + form.word2 + "&meaning2" + form.meaning2
          + "&word3=" + form.word3 + "&meaning3" + form.meaning3
          + "&word4=" + form.word4 + "&meaning4" + form.meaning4
          + "&word5=" + form.word5 + "&meaning5" + form.meaning5,

        headers: {

        }
      }).then((res) => {
        if (res.data.status == 200) {
          Modal.success({
            title: 'Success',
            content: res.data.info
          });
        } else {
          Modal.error({
            title: 'Error',
            content: res.data.info
          });
        }
      })

    }


    return {
      layout,
      form,
      handleSubmit,
    }
  },
}
</script>