<template>
    <div style="width : 65%">
        <a-typography-title class="title">
            Deck Search
        </a-typography-title>
        <a-form :model="form" :layout="layout" class="form-box">
            <a-form-item field="search" label="search">
                <a-input v-model="form.search" placeholder="input your query" />
            </a-form-item>

            <a-form-item>
                <a-button @click="searchMethod">Submit</a-button>
            </a-form-item>
        </a-form>
    </div>
</template>

<script>

import { inject, ref, reactive } from 'vue'
import { Modal } from '@arco-design/web-vue';
import router from '@/router';

export default {
    name:'SearchComponent',
    setup() {
        const layout = ref('vertical')
        const form = reactive({});
        const $axios = inject("$axios");
        const word=ref();
        const meaning=ref();

        const searchMethod = () => {
            $axios({
                url: "",
                method: "post",
                data: "deck_name=" + form.search ,
                headers: {}
            }).then((res) => {
                if (res.data.status == 200) {
                    console.log(res);
                word.value = res.data.data.word;
                meaning.value = res.data.data.meaning;

                    router.push("/DeleteEdit");
                } else {
                    Modal.error({
                        title: 'Error',
                        content: res.data.info
                    });
                }
            })
        }

        return {
            layout,
            form,
            word,
            meaning,
            searchMethod
        }
    }
}
</script>

<style>
.title {
    margin-left: 2%;
    text-align: left;
}

/* .arco-layout-content {
    display: inline;
} */

.form-box {
    margin-left: 2%;
    text-align: left;
}
</style>