<template>
    <a-form :model="form" class="infor-form">
        <a-form-item label="Deck Name">
            <a-typography-text>
                {{ deck_name }}
            </a-typography-text>
        </a-form-item>

        <a-form-item label="Words">
            <a-typography-text>
                {{ word }}
            </a-typography-text>
        </a-form-item>

           <a-form-item label="Meaning">
            <a-typography-text>
                {{ meaning }}
            </a-typography-text>
        </a-form-item>

    </a-form>
</template>

<script>

import { inject, onMounted, ref } from 'vue'
export default {
    setup() {
        const deck_name = ref();
        const word = ref();
        const meaning = ref();
    
        const $axios = inject("$axios");

        
        onMounted(() => {
            $axios({
                url:"/anki/showDecks.do",
                method:"post",
                headers : {}
            }).then((res)=> {
                console.log(res);
                deck_name.value = res.data.data.deck_name;
                word.value = res.data.data.word;
                meaning.value = res.data.data.realname;
              
            })
        })

       

        return {
            deck_name,
            word,
            meaning,
  
        }
    },
}
</script>

<style scoped>
.infor-form {
    width: 100%;
    height: 100%;
}
</style>